"use client"
import { MARGIN_TOP, SIDEBAR_WIDTH } from '@/src/constants/constants'
import React, {JSX, useEffect, useState} from 'react'
import { ParamValue } from 'next/dist/server/request/params' 
import { useParams } from 'next/navigation'
import { User } from '@/src/types'
import extractErrorMessages from '@/src/app/utils/error.utils'
import axios from 'axios'
import { AuthService } from '@/src/services/auth.service'
import UserDetail from '@/src/components/pages/users/UserDetail'
import ActivityService from '@/src/services/action.service'
import { Activity } from '@/src/types/action/action.types'

const page = (): JSX.Element => {
  const [userData, setUserData] = useState<User | null>(null)
  const [err, setErr] = useState<string[] | string | null>();
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false)
  const [activityData, setActivityData] = useState<Activity[]>([])
  const [currentPage, setCurrentPage] = useState<number>(1)
  const [pageLimit, setPageLimit] = useState<number>(20)
  const [totalPages, setTotalPages] = useState<number>(1)

  const id: ParamValue = useParams().id

  const getUser = async(id: ParamValue): Promise<void>=>{
    setIsLoading(true)
    setErr(null)
    try {
      const result = await AuthService.getUserById(id)

      if (result.status === 200){
        setUserData(result.data.data)
      }
    } catch (error) {
      process.env.NEXT_PUBLIC_ENV === "development" && console.error(error);
      if (axios.isAxiosError(error)) {
        const messages = extractErrorMessages(error);
        setErr(messages);
      } else {
        setErr(["Something went wrong"]);
      }
    } finally {
      setIsLoading(false)
    }
  }

  

  const userActivityData = async(id:string)=>{
    setLoading(true)
     try {

      const result = await ActivityService.getByUser(id, currentPage, pageLimit)

      if(result.status ===200){

        const activityData = result.data.data;

        setActivityData(activityData)

      }
      
     } catch (error) {
      process.env.NEXT_PUBLIC_ENV === "development" && console.error(error);
      if (axios.isAxiosError(error)) {
        const messages = extractErrorMessages(error);
        setErr(messages);
      } else {
        setErr(["Something went wrong"]);
      }
     } finally {
      setIsLoading(false)
     }
  }


  useEffect(() => {
    if(!id) return
    getUser(id)

    userActivityData(String(id))
  }, [])

  
  

  return (
    <div className={`ml-72 mt-14 p-6`}>
       <UserDetail userData={userData} isLoading={isLoading}/>
      
    </div>
  )
}

export default page
